.modalLogout {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: 0.5s;
  pointer-events: none;
}
.modalActiveLogout {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: 0.5s;
}
.modal__contentLogout {
  padding: 50px;
  border-radius: 12px;
  background-color: #161f37;
  max-height: 215px;
  max-width: 500px;
  position: relative;
  overflow: hidden;
  transform: scale(0);
  transition: 0.5s;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.modal__contentActiveLogout {
  padding: 50px;
  border-radius: 12px;
  background-color: #161f37;
  max-height: 215px;
  max-width: 500px;
  position: relative;
  overflow: hidden;
  transform: scale(1);
  transition: 0.5s;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.closeBtn {
  width: 32px;
  height: 32px;
  color: #f3f3f3;
  position: absolute;
  right: 18px;
  top: 18px;
}
.titleLogout {
  color: #f3f3f3;
  font-size: 18px;
  line-height: calc(18 / 24);
  text-align: center;
  /* width: 263px; */
  font-weight: 300;
  margin-bottom: 37px;
}
.btnsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  width: 400px;
  height: 54px;
}
.logoutBtn {
  background-color: #f3f3f3;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 42px;
  color: #161f37;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.logoutBtn:hover,
.logoutBtn:focus {
  transform: scale(1.03);
  border: 1px solid #ffffff5f;
}
.cancelBtn {
  background-color: #434d67;
  border-color: #434d67;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 42px;
  color: #f3f3f3;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.cancelBtn:hover,
.cancelBtn:focus,
.cancelBtn:active {
  transform: scale(1.03);
}
